
const Parroquia = () => {
    return (  
        <>
        
        <div className="cont-cont mb-5">
            <div className="cont">
                <h1 className="title-hist">Parroquia</h1>

                <img className="image_parroquia" src={process.env.PUBLIC_URL+ `/imagenes/parroquia.jpeg`} alt="img" />
                <h3 className="title-hist2">UNA DIÓCESIS, UN BARRIO, UNA PARROQUIA, UNA COMUNIDAD.</h3>
                <p>En la ciudad de Mar del Plata cada vez que pasamos por la diagonal Lisandro de la torre nos encontramos con un templo de características edilicias fuera de lo común para nuestro concepto de templo católico, erigido en una parcela poco común ya que tiene forma de triángulo distando muchísimo de la convencional “cuadradidad”; sabemos que pertenece a la iglesia católica ya que cuenta con la imagen de nuestra madre en la entrada, pero … ¿cuánto sabemos de nuestra parroquia? ¿cuándo se edificó? ¿Porque tiene esa forma? ¿Está consagrada?  ¿Quiénes la mantienen ¿qué hacen los que concurren continuamente? trataremos de responder a algunas de estas preguntas. </p>
                <h5 className=" mt-5 mb-5">Una diócesis</h5>
                <p>¿Qué es una Diócesis? Es una iglesia particular definida por el Código de Derecho Canónico como la porción del pueblo de Dios, circunscrita territorialmente y cuyo cuidado pastoral es encomendado a un obispo. (Canon 369) </p>
                <p>Comodoro Rivadavia, Formosa, Gualeguaychú, Lomas de Zamora, Mar del Plata, Morón, Nueve de Julio, Posadas, Reconquista, San Isidro, Santa Rosa, Villa María. Uno por uno, y por orden alfabético, fueron surgiendo los nombres de las nuevas diócesis que, por la bula “Quandoquidem adoranda” Pío XII había erigido en la Argentina. </p>
                <p>Era el 11 de febrero de 1957, Fiesta de Nuestra Señora de Lourdes y, después de mucho tiempo, un sueño que se hacía realidad. Y al frente de esta nueva Diócesis fue puesto nuestro primer Obispo Monseñor Enrique Rau. </p>
                <p> ( extracto del diario la capital<a href=" https://www.lacapitalmdp.com/creacion-de-la-diocesis-de-mar-del-plata/"> Click aquí</a> ) </p>
                
                <h5 className=" mt-5 mb-5" >Un barrio </h5>

                <p>El barrio donde se encuentra nuestra parroquia es el llamado Peralta Ramos Oeste, pero conocido públicamente como el “barrio mundialista” ya que contiene en su territorio el estadio José María Minella, el cual fue construido para el mundial disputado en nuestro país en el año 1978. Ahora bien, para quienes nos identificamos con nuestra parroquia decimos que estamos en <b> “el barrio del obrero.” </b> </p>
                <h5 className=" mt-5 mb-5">Una parroquia, el templo</h5>

                <p>En 1957, el día 12 de marzo fue erigida por S.E.R Mons. Enrique Rau la parroquia Jesús Obrero, pero aun no disponía de un templo, ya que tuvo que esperar hasta fines de 1974 para comenzar a construirse, cuenta el arquitecto Jorge Kuyznetzov que el padre Manolo le propuso construir el templo a fuerza de donaciones. Y este sacerdote, firme e insistente intentó recaudar mediante campañas y actividades los fondos, pero con resultados lejos de lo necesario. Así pasaron meses. </p>
                <p>Cuenta el arquitecto: – “Yo me olvidé del asunto. Hasta que una madrugada a fines del 74 me despierto con el llamado telefónico del padre, quien disculpándose por el horario dijo que no podía de contener su alegría, y compartirla conmigo por haber conseguido el dinero para la Capilla”. La noticia me alegró por un lado, pero me estremeció por el otro, ya que ahora sí, tenía que cumplir con la promesa.” </p>
                <p> “Una sola condición nos preocupaba, es que la donación prometida era condicional, a saber, el dinero prometido será para la cubierta, y una vez que la construcción esté en condiciones de colocarla.” </p>
                <p> “El resto de esa noche fue el insomnio, frente a una situación de cómo llegar al techo sin dinero, sumado a la curiosa forma de donación, y a su vez encontrar una solución de diseño que pueda superar esas dificultades. Cuando de pronto en mi mente entredormida se apareció la imagen tridimensional de un ave que descendía desde lo alto posándose sobre el terreno señalado.  De un sobresalto corrí al tablero y lo materialicé mediante el recurso de los nuevos materiales de la época, que era el canalón de nueve metros de longitud, con el detalle que el mismo lo colocaría  desde el piso en forma casi vertical en el extremo más angosto del terreno, y se abriría por tramos a lo largo del lote , solucionando el ingreso de la iluminación entre los tramos por un lado, y lo más importante es que de esta manera, el edificio  “era el techo”, pronto para recibir la donación conseguida.” “Sin demorar el padre Manolo reunió a la comunidad para hacer rapidísimo los cimientos y las bases para el techo. Mucho entusiasmo por la solución hallada, y a la expectativa para el cumplimiento de la promesa. </p>
                <p><a href="http://fotosviejasdemardelplata.blogspot.com/2016/12/iglesia-jesus-obrero.html">http://fotosviejasdemardelplata.blogspot.com/2016/12/iglesia-jesus-obrero.html</a></p>
                
                <h6 className="mt-5 mb-3">Parroquia Jesús Obrero década de 70</h6>
                <img className="image_frente2" src={process.env.PUBLIC_URL+ `/imagenes/parroquia70.jpg`} alt="img" />
                <p>En la actualidad el templo se encuentra en una etapa de puesta en valor, hasta el día de la fecha se han realizado tanto mejoras como arreglos: </p>
                <ul>
                <li>Limpieza interior del techo </li>
                <li>Arreglo y pintura exterior </li>
                <li>Instalación de calefacción central en todo el edificio </li>
                <li>Revestimiento del altar </li>
                <li>Restauración de imágenes (Jesús obrero, sagrado corazón de Jesús, nuestra señora de lujan) </li>
                <li>Pulido del piso del presbiterio</li>
                <li>Colocación del vitral (actualmente en proceso) </li>
                <li>Luces</li>
                <li>instalación de mecheros y bacha en la cocina (para la noche de la caridad) entre otros. </li>
                </ul>
                <h5 className=" mt-5 mb-5" >Una comunidad </h5>
                <p>La comunidad parroquial está compuesta por laicos que generosamente comprometen su tiempo al servicio de la parroquia, trabajando en los distintos grupos pastorales los podemos enumerar en distintas tareas pastorales a saber:</p>
                <ul>
                   <li> Secretaría</li>
                   <li>  Catequesis </li> 
                   <li> Caritas</li> 
                   <li>  Noche de la caridad </li> 
                   <li>  Apostolado de la oración</li>  
                   <li>  Renovación carismática católica </li> 
                   <li>  Liturgia</li> 
                   <li>  Circulo Bíblico</li> 
                   <li>  Movimiento Familia Cristiana</li> 
                   <li>  Acción Católica Argentina</li> 
                   <li>  Madres Mónicas</li> 
                   <li>  Grupo “Obreros de María”</li> 
                   <li>  Grupo Scout San Francisco de Asís </li> 

                </ul>
                <p>
                Otra característica de nuestra parroquia es contar con el colegio con sus tres niveles de educación con el personal docente que no solo cuenta con nivel académico sino con un corazón dispuesto a la tarea,  pero no estaríamos hablando de comunidad si no contamos a todos  los que se acercan o quisieran acercarse a nuestra querida parroquia como dice la palabra “ la cosecha es abundante pero los trabajadores son pocos” (Lc 10, 1-9)  es por eso que para dar una mano no necesitamos ser eruditos o religiosos o tener algún título , solo tener ganas de participar y dar una mano y el señor los recompensara al ciento por uno
                </p>
                <p>Terminamos esta reseña con una frase de San Juan Pablo II <i>“no tengan miedo, ábranle las puertas de par en par a Dios”</i> y así es nuestra parroquia con sus puertas abiertas de par en par para recibir a quien quiera acercarse convencidos en que en cada uno de estos hermanos es Jesús mismo quien viene al encuentro.</p>

                <p>Si queres conocer sobre las actividades, celebraciones y eventos de TU PARROQUIA te invitamos a comunicarte con nosotros y seguirnos por las redes:</p>
                <p>Facebook: <a href="https://www.facebook.com/profile.php?id=100009619770356">Parroquia Jesus Obrero</a></p>
                <p>Instagram: <a href="https://www.instagram.com/jesusobreromdp/">@jesusobreromdp</a></p>
                <p>WhatsApp: 2236855887</p>

                <h4 className="mt-5 mb-2">PARROQUIA CRISTO RESUCITADO</h4>

                <img className="image_parroquia2" src={process.env.PUBLIC_URL+ `/imagenes/parroquiacristoresucitado.jpg`} alt="img" />


             </div>

             
        </div>
        
        </>
    );
}
 
export default Parroquia;