

const Historia = () => {
    return (  
        <>
        
        <div className="cont-cont mb-5">
            <div className="cont">
                <h1 className="title-hist">Historia</h1>
               
                <p>El Instituto Jesús Obrero es una institución que cuenta con los tres niveles de enseñanza: Inicial, Primario y Secundario. Fundada en el año 1959 pasaron por sus aulas cantidad innumerables de estudiantes </p>
                <p>El espíritu de la Institución, su historia y sus actividades se pueden sentir y vivenciar desde el momento que se ingresa al edificio. Todo al servicio de los estudiantes, quienes son los protagonistas de la historia de esta institución. Por ellos transcurren las acciones que hicieron y hacen que esta establecimiento sea reconocido y considerado dentro del barrio que lo vio nacer, crecer y convertirse en lo que es hoy. </p>
                <p>En el año 1959 comienza a funcional el Nivel Primario, en el año 1961 abre sus puertas el Jardín de Infantes y en el año 1962 comienza a funcionar el Nivel Secundario. </p>
                <p>Desde su fundador, el Padre Pedro María Arburua, todos sus sucesores, docentes, personal administrativo, de maestranza han colaborado para que la Institución crezca y se resignifique en el tiempo. </p>
                <p>Entendemos  la dimensión pastoral de la comunidad educativa como un modo de ser que “educa hacia un proyecto de ser humano en el que habite Jesucristo con el poder transformador de su vida nueva.”Este “proyecto comporta la tarea de la comunidad educativa entera, incluidos los padres, poniendo siempre en el centro a los estudiantes, que crezcan juntos, en el respeto y al ritmo de cada uno”. </p>
                <p>La dimensión pastoral si bien incluye los contenidos propios de la materia catequesis o formación religiosa, no se reduce solo a esos espacios, sino que busca impregnar todo la vida de la comunidad educativa en  “la premura de educar con una síntesis entre fe, cultura y vida”. </p>
                <p>Aquellas familias que profesan otro credo, o están en búsqueda de su vivencia de la trascendencia, son recibidas en el respeto de su libertad y sus hijos participan de las materias de catequesis como espacio de formación humana, así como de actividades religiosas sin socavamiento de su identidad. </p>
                <p>Nuestro proyecto pastoral  tiende a “promover la formación integral de la persona teniendo su fundamento en Cristo, con identidad eclesial y cultural, y con excelencia académica. Además, han de generar solidaridad y caridad con los más pobres. El acompañamiento de los procesos educativos, la participación en ellos de los padres de familia, y la formación de docentes, son tareas prioritarias de la pastoral educativa.” </p>
                <p>Este proyecto educativo tiene como elementos irrenunciables en su práctica y son su identidad: la pedagogía del encuentro,  la jerarquía de verdades de fe, la pedagogía del crecimiento y la maduración, del bien posible, la verdadera inclusión-integración en la acción pastoral. </p>
                <p>Todo esto, iluminado desde una mística de Iglesia Sinodal en todos los niveles y estamentos, en la ejecución de idearios y proyectos educativos, al servicio de todos, en la solución pacífica de urgencias y coyunturas particulares, en la renovación constante de los vínculos. </p>
                <p> “La identidad y la mística deben estar por sobre cualquier tema particular que afecte a la comunidad educativa. De estas dos surgen las políticas educativas de todas y cada unas de las Escuelas Episcopales”. </p>
                <p>Entendemos que el  proyecto educativo es un camino que busca no solo el acompañamiento explícito de los integrantes de la comunidad  sino también del anuncio “en salida” como dinámica del encuentro con Jesucristo. Buscando gestar con la fuerza del Espíritu comunidades orantes, fraternas y misioneras, sabiendo que este proyecto pastoral educativo nos compromete a todos, en la búsqueda de un mundo nuevo donde  “no habrá continente nuevo sin hombres nuevos, que a la luz del Evangelio sepan ser verdaderamente libres y responsables” </p>
                <p><b>Nivel inicial:</b> Es el inicio del eslabón de los niveles. En él se desarrollan espacios de apertura  y múltiples propuestas, Junto a los niños, la familia participa en diferentes actividades como: muestras de arte, actos escolares, salidas  y jornadas educativas. Tanto los directivos como docentes forman un gran equipo de trabajo que hacen posible que año tras año se siga creciendo y enriqueciendo con el fin de lograr una mejor calidad educativa</p>
                <p><b>Nivel Primario:</b> Es la continuación del nivel anterior. En el, comienza a moldearse el espíritu académico y la esencia de los valores que los niños vivencian en su caminar. Este nivel se focaliza en el trabajo de la alfabetización y en la construcción del alumno/ a para descubrir los senderos del saber. Junto a las docentes, familias y chicos se desarrollan las siguientes actividades: Charlas educativas, Feria de Ciencia, Maratón de lectura temática. Actos escolares, Salidas educativas. </p>
                <p><b>Nivel secundario:</b> Es el último eslabón de la cadena dentro de la comunidad educativa. Se trabaja en función de que el egresado sea un sujeto crítico, colaborativo, participativo, con capacidad de discernimiento, de escucha, con un espíritu de crecimiento  basado en el compromiso y el sentido de pertenencia. Junto a los docentes, familias y estudiantes  se desarrollan las siguientes actividades: Grupo de Investigación de Historia Oral, Talleres, Convivencias, Campamentos y Viajes de estudios. </p>

             </div>
             
        </div>
        
        </>
    );
}
 
export default Historia;